import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import background from "../resources/videos/blurredParticles.mp4"
import "../CSS/login.css"

export const LoginPage = ({ authenticate }) => {
    const [inpUsername, setUsername] = useState("");
    const [inpPassword, setPassword] = useState("");

    const navigate = useNavigate()
    const auth = () => {
        authenticate()
        navigate('/dashboard')
    }
    const checkinp = () => {
        if(inpUsername.split(" ").join("") === 'admin' && inpPassword === 'M1KhBui2019g!') {
            auth()
        } else {
            alert("Das angegebene Passwort ist nicht richtig!")
        }
    }
    return (
    <div className="container">
        <div className="login">
            <video autoPlay disablePictureInPicture loop muted style={{
                    position: "absolute",
                    width: "100%",
                    left: "50%",
                    top: "50%",
                    height: "100%",
                    objectFit: "cover",
                    transform: "translate(-50%, -50%)",
                    zIndex: "-1"
                }}>
                    <source src={background} type="video/mp4"/>
                </video>
                <form className="box">
                    <h1>Login</h1>
                    <input type="text" id="username" placeholder="Benutzername" value={inpUsername} onChange={(e) => setUsername(e.target.value)}/>
                    <input type="password" id="userpassword" placeholder="Passwort" value={inpPassword} onChange={(e) => setPassword(e.target.value)}/>
                    <button onClick={checkinp}>Weiter</button>
                    
            </form> 
        </div>
    </div>
    )
}